const actions = {
  getPettyCashes: (page, search, filter) => ({
    type: 'GET_PETTY_CASHES_REQUEST',
    payload: {
      page: page,
      search: search,
      filter: filter
    }
  }),
  createPettyCash: () => ({
    type: 'CREATE_PETTY_CASH_REQUEST'
  }),
  storePettyCash: data => ({
    type: 'STORE_PETTY_CASH_REQUEST',
    payload: { body: data }
  }),
  editPettyCash: id_petty_cash => ({
    type: 'EDIT_PETTY_CASH_REQUEST',
    payload: { id_petty_cash: id_petty_cash }
  }),
  updatePettyCash: (id_petty_cash, data) => ({
    type: 'UPDATE_PETTY_CASH_REQUEST',
    payload: {
      id_petty_cash: id_petty_cash,
      body: data
    }
  }),
  showPettyCash: id_petty_cash => ({
    type: 'SHOW_PETTY_CASH_REQUEST',
    payload: { id_petty_cash: id_petty_cash }
  }),
  processPettyCash: id_petty_cash => ({
    type: "PROCESS_PETTY_CASH_REQUEST",
    payload: { id_petty_cash }
  }),
  cancelPettyCash: (id_petty_cash, data) => ({
    type: "CANCEL_PETTY_CASH_REQUEST",
    payload: { 
      id_petty_cash,
      body : data
    }
  }),
  uploadDocument: (id_petty_cash, data) => ({
    type: "UPLOAD_PETTY_CASH_DOCUMENT_REQUEST",
    payload: {
      id_petty_cash,
      body : data
    }
  }),
  downloadDocument: id_petty_cash_document => ({
    type: "DOWNLOAD_PETTY_CASH_DOCUMENT_REQUEST",
    payload: {id_petty_cash_document}
  }),
  deleteDocument: id_petty_cash_document => ({
    type: "DELETE_PETTY_CASH_DOCUMENT_REQUEST",
    payload: { id_petty_cash_document }
  }),
  clearSuccess: () => ({
    type: "CLEAR_SUCCESS"
  }),
  clearError: () => ({
    type: "CLEAR_ERROR"
  }),
  clearErrorField: () => ({
    type: "CLEAR_ERROR_FIELD"
  })
};

export default actions;
