import React, { Component } from "react";
import IntlMessages from "../../../../components/utility/intlMessages";
import getLanguage from "../../../App/MyLanguange";
import { Form, Input, Col, Modal, Select, Checkbox, Radio } from "antd";

const { Option } = Select;
const InputGroup = Input.Group;

class ModalAddPettyCashType extends Component {
  state = {
    check_code: false
  };

  componentDidUpdate = () => {
    const { resetFields } = this.props.form;
    const { success } = this.props.pettyCashType;
    if (success) {
      resetFields();
      this.setState({
        check_code: false
      });
    }
  };

  handleChangeCode = () => {
    const { setFieldsValue } = this.props.form;
    this.setState({ check_code: !this.state.check_code });
    if (this.state.check_code === false) {
      setFieldsValue({
        ["petty_cash_type_code"]: null
      });
    }
  };

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFieldsAndScroll((err, values) => {
      if (!err) {
        this.props.submit(values);
      }
    });
  };

  render() {
    const { getFieldDecorator, getFieldError } = this.props.form;
    const { pettyCashType, pettyCash } = this.props;
    const loading = pettyCashType.loading;
    const { accounts } = pettyCash;
    const error = pettyCashType.errorField ? pettyCashType.errorField : [];

    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    };

    const radioGroupStyle = {
      display: "flex",
      width: "50%"
    };

    const radioButtonStyle = {
      flex: "1",
      textAlign: "center"
    };

    return (
      <Modal
        title={<IntlMessages id="pettyCashTypes.add.title" />}
        visible={this.props.visible}
        centered
        onCancel={this.props.handleModal}
        onOk={this.handleSubmit}
        width="50%"
        okText={getLanguage({ id: "button.submit" })}
        cancelText={getLanguage({ id: "button.cancel" })}
        okButtonProps={{ loading: loading }}
        cancelButtonProps={{ loading: loading }}
      >
        <Form layout="vertical" onSubmit={this.handleSubmit}>
          <div className="isoDetailDataFlex" style={{ padding: "0" }}>
            <div
              className="isoColumn-2 isoFormGroupComp"
              style={{ padding: "0 15px 0 0" }}
            >
              <InputGroup compact>
                <Col span={20}>
                  <Form.Item
                    {...formItemLayout}
                    label={<IntlMessages id="pettyCashTypes.add.code" />}
                    validateStatus={
                      error.petty_cash_type_code ||
                      getFieldError("petty_cash_type_code")
                        ? "error"
                        : null
                    }
                    help={
                      error.petty_cash_type_code
                        ? error.petty_cash_type_code
                        : getFieldError("petty_cash_type_code")
                    }
                  >
                    {getFieldDecorator("petty_cash_type_code", {
                      rules: [
                        {
                          max: 30,
                          message: <IntlMessages id="maxLength30" />
                        }
                      ]
                    })(
                      <Input disabled={this.state.check_code ? true : false} />
                    )}
                  </Form.Item>
                </Col>
                <Col span={4}>
                  <Form.Item
                    {...formItemLayout}
                    label={<IntlMessages id="autoGenerateCode" />}
                  >
                    <Checkbox
                      style={{
                        paddingTop: "5px",
                        paddingLeft: "8px"
                      }}
                      checked={this.state.check_code}
                      onChange={this.handleChangeCode}
                    />
                  </Form.Item>
                </Col>
              </InputGroup>

              <Form.Item {...formItemLayout} label={<IntlMessages id="name" />}>
                {getFieldDecorator("description", {
                  rules: [
                    {
                      required: true,
                      message: <IntlMessages id="required" />
                    },
                    {
                      max: 50,
                      message: <IntlMessages id="maxLength50" />
                    }
                  ]
                })(<Input />)}
              </Form.Item>

              <Form.Item
                {...formItemLayout}
                label={<IntlMessages id="accounts.pageHeader" />}
              >
                {getFieldDecorator("id_account", {
                  rules: [
                    {
                      required: true,
                      message: <IntlMessages id="required" />
                    }
                  ]
                })(
                  <Select
                    loading={this.props.pettyCash.loading}
                    showSearch
                    optionFilterProp="children"
                  >
                    {accounts.map((data, key) => (
                      <Option key={key} value={data.id_account}>
                        {data.account_code + " " + data.account_name}
                      </Option>
                    ))}
                  </Select>
                )}
              </Form.Item>

              <Form.Item
                {...formItemLayout}
                label={<IntlMessages id="transactionType" />}
              >
                {getFieldDecorator("transaction_type", {
                  rules: [
                    {
                      required: true,
                      message: <IntlMessages id="required" />
                    }
                  ]
                })(
                  <Radio.Group buttonStyle="solid" style={radioGroupStyle}>
                    <Radio.Button style={radioButtonStyle} value={1}>
                      <IntlMessages id="deposit" />
                    </Radio.Button>
                    <Radio.Button style={radioButtonStyle} value={2}>
                      <IntlMessages id="withdrawal" />
                    </Radio.Button>
                  </Radio.Group>
                )}
              </Form.Item>
            </div>
          </div>
        </Form>
      </Modal>
    );
  }
}

const WrappedModalAddPettyCashType = Form.create({
  name: "modal_add_petty_cash_type"
})(ModalAddPettyCashType);

export default WrappedModalAddPettyCashType;
