import React, { Component } from "react";
import moment from "moment";
import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import { Form, Input, Select, InputNumber, DatePicker, Radio } from "antd";
import getLanguage from "../../../App/MyLanguange";
import ModalAddPettyCashType from "./modalAddPettyCashType";

const { Option } = Select;
const TextArea = Input.TextArea;

class PettyCashCreateForm extends Component {
  handleChangePettyCashType = value => {
    const { setFieldsValue } = this.props.form;
    const { petty_cash_types } = this.props.pettyCash;

    petty_cash_types.map(petty_cash_type => {
      if (petty_cash_type.id_petty_cash_type === value) {
        setFieldsValue({
          transaction_type: petty_cash_type.transaction_type
        });
      }
    });
  };

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFieldsAndScroll((err, values) => {
      if (!err) {
        values.transaction_date = values.transaction_date.format(
          "YYYY-MM-DD HH:mm:ss"
        );
        this.props.submit(values);
      }
    });
  };

  render() {
    const { getFieldDecorator } = this.props.form;
    const { pettyCash, user } = this.props;
    const { loading } = pettyCash;

    let permissions = this.props.permissions;

    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    };
    const tailFormItemLayout = {
      wrapperCol: {
        xs: {
          span: 24,
          offset: 0
        },
        sm: {
          span: 24,
          offset: 0
        }
      }
    };

    const radioGroupStyle = {
      display: "flex",
      width: "100%"
    };

    const radioButtonStyle = {
      flex: "1",
      textAlign: "center"
    };

    return (
      <>
        <ModalAddPettyCashType
          handleModal={this.props.handleModalAddPettyCashType}
          pettyCashType={this.props.pettyCashType}
          pettyCash={this.props.pettyCash}
          submit={this.props.handleSubmitPettyCashType}
          visible={this.props.visibleAddPettyCashType}
        />
        <Form layout="vertical" onSubmit={this.handleSubmit}>
          <div className="isoDetailDataFlex" style={{ padding: "0" }}>
            <div
              className="isoColumn-2 isoFormGroupComp"
              style={{ padding: "0 15px 0 0" }}
            >
              <Form.Item
                {...formItemLayout}
                label={<IntlMessages id="purchaseOrders.transactionDate" />}
              >
                {getFieldDecorator("transaction_date", {
                  initialValue: moment(),
                  rules: [
                    {
                      required: true,
                      message: <IntlMessages id="required" />
                    }
                  ]
                })(
                  <DatePicker
                    style={{ width: "100%" }}
                    showTime
                    format="DD MMMM YYYY HH:mm:ss"
                    onChange={this.handleChangeTransDate}
                    onOpenChange={this.handleChangeTransDate}
                    disabled={
                      !permissions.includes("Can Back Date") ? true : false
                    }
                    allowClear={false}
                    disabledDate={e => e.valueOf() > moment()}
                  />
                )}
              </Form.Item>

              <Form.Item
                {...formItemLayout}
                label={<IntlMessages id="pettyCashType" />}
              >
                {getFieldDecorator("id_petty_cash_type", {
                  rules: [
                    {
                      required: true,
                      message: <IntlMessages id="required" />
                    }
                  ]
                })(
                  <Select
                    loading={this.props.pettyCash.loading}
                    showSearch
                    optionFilterProp="children"
                    onChange={this.handleChangePettyCashType}
                  >
                    {permissions.includes("Create Petty Cash Type") ? (
                      <Option
                        key={"Create Petty Cash Type"}
                        value={undefined}
                        onClick={() => this.props.handleModalAddPettyCashType()}
                      >
                        <div style={{ color: "#1890FF", fontStyle: "italic" }}>
                          {" + " +
                            getLanguage({
                              id: "pettyCashTypes.pageHeader"
                            })}
                        </div>
                      </Option>
                    ) : null}

                    {pettyCash.petty_cash_types.map((data, key) => (
                      <Option key={key} value={data.id_petty_cash_type}>
                        {data.petty_cash_type_code +
                          " (" +
                          data.description +
                          ")"}
                      </Option>
                    ))}
                  </Select>
                )}
              </Form.Item>

              <Form.Item
                {...formItemLayout}
                label={<IntlMessages id="transactionType" />}
              >
                {getFieldDecorator("transaction_type", {
                  //initialValue: data.cash_flow_type,
                  rules: [
                    {
                      required: true,
                      message: <IntlMessages id="required" />
                    }
                  ]
                })(
                  <Radio.Group
                    buttonStyle="solid"
                    disabled={true}
                    style={radioGroupStyle}
                  >
                    <Radio.Button style={radioButtonStyle} value={1}>
                      <IntlMessages id="deposit" />
                    </Radio.Button>
                    <Radio.Button style={radioButtonStyle} value={2}>
                      <IntlMessages id="withdrawal" />
                    </Radio.Button>
                  </Radio.Group>
                )}
              </Form.Item>

              <Form.Item
                {...formItemLayout}
                label={<IntlMessages id="amount" />}
              >
                {getFieldDecorator("amount", {
                  //initialValue: data.amount,
                  // initialValue: 0,
                  rules: [
                    {
                      required: true,
                      message: <IntlMessages id="required" />
                    }
                  ]
                })(
                  <InputNumber
                    formatter={value =>
                      `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ",")
                    }
                    parser={value => value.replace(/\$\s?|(,*)/g, "")}
                    min={0.01}
                    precision={2}
                    style={{ width: "100%" }}
                  />
                )}
              </Form.Item>
            </div>
            <div
              className="isoColumn-2 isoFormGroupComp"
              style={{ padding: "0 0 0 15px" }}
            >
              <Form.Item
                {...formItemLayout}
                label={<IntlMessages id="locations.pageHeader" />}
              >
                {getFieldDecorator("id_location", {
                  initialValue:
                    user && user.default_employee_location
                      ? user.default_employee_location.id_location
                      : null,
                  rules: [
                    {
                      required: true,
                      message: <IntlMessages id="required" />
                    }
                  ]
                })(
                  <Select
                    loading={this.props.pettyCash.loading}
                    showSearch
                    optionFilterProp="children"
                  >
                    {pettyCash.locations.map((data, key) => (
                      <Option key={key} value={data.id_location}>
                        {data.location_code + " (" + data.location_name + ")"}
                      </Option>
                    ))}
                  </Select>
                )}
              </Form.Item>

              <Form.Item
                {...formItemLayout}
                label={<IntlMessages id="employees.pageHeader" />}
              >
                {getFieldDecorator(
                  "id_employee",
                  {}
                )(
                  <Select
                    loading={this.props.pettyCash.loading}
                    showSearch
                    optionFilterProp="children"
                    allowClear={true}
                    //disabled={used ? true : false}
                  >
                    {pettyCash.employees.map((data, key) => (
                      <Option key={key} value={data.id_employee}>
                        {data.employee_code +
                          " (" +
                          data.first_name +
                          (data.last_name ? " " + data.last_name : "") +
                          ")"}
                      </Option>
                    ))}
                  </Select>
                )}
              </Form.Item>

              <Form.Item
                {...formItemLayout}
                label={<IntlMessages id="referenceNo" />}
              >
                {getFieldDecorator("reference_no", {})(<Input />)}
              </Form.Item>

              <Form.Item {...formItemLayout} label={<IntlMessages id="note" />}>
                {getFieldDecorator(
                  "note",
                  {}
                )(<TextArea autosize={{ minRows: 3 }} />)}
              </Form.Item>
            </div>
          </div>

          <Form.Item {...tailFormItemLayout}>
            <Button
              type="primary"
              htmlType="submit"
              style={{ marginTop: "20px" }}
              loading={loading}
            >
              <IntlMessages id="button.submit" />
            </Button>
          </Form.Item>
        </Form>
      </>
    );
  }
}

const WrappedPettyCashCreateForm = Form.create({ name: "create_petty_cash" })(
  PettyCashCreateForm
);

export default WrappedPettyCashCreateForm;
