import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";
import PettyCashCreateForm from "./createForm";
import notification from "../../../../components/notification";
import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import PageHeader from "../../../../components/utility/pageHeader";
import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import actions from "../../../../redux/financial/pettyCash/actions";
import pettyCashTypeActions from "../../../../redux/cashBank/pettyCashType/actions";
import { Box } from "../../../App/app.style";
import { Tabs } from "antd";

const TabPane = Tabs.TabPane;
const redirectTo = "/dashboard/financials/petty-cashes";
const { storePettyCashType } = pettyCashTypeActions;

class CreatePettyCash extends Component {
  state = {
    visibleAddPettyCashType: false
  };
  // Get All data diawal
  componentDidMount() {
    const { createPettyCash } = this.props;
    createPettyCash();
  }

  componentDidUpdate() {
    // Display error notif
    if (this.props.pettyCash.error) {
      notification("error", this.props.pettyCash.error);
      this.props.clearError();
    }
    // Display success notif
    if (this.props.pettyCash.success) {
      this.props.history.replace(
        redirectTo + "/update/" + this.props.pettyCash.id_petty_cash
      );
    }

    // Petty Cash Type
    if (this.props.pettyCashType.error) {
      notification("error", this.props.pettyCashType.error);
      this.props.clearError();
    }

    if (this.props.pettyCashType.success) {
      const { createPettyCash, clearSuccess } = this.props;
      notification("success", this.props.pettyCashType.success);
      this.handleModalAddPettyCashType();
      clearSuccess();
      createPettyCash();
    }
  }

  handleModalAddPettyCashType = () => {
    this.setState({
      visibleAddPettyCashType: !this.state.visibleAddPettyCashType
    });
  };

  handleSubmit = value => {
    const { storePettyCash } = this.props;
    storePettyCash(value);
  };

  handleSubmitPettyCashType = value => {
    const { storePettyCashType } = this.props;
    storePettyCashType(value);
  };

  render() {
    const { user } = this.props.auth;
    let { permissions } = user ? user : [];
    permissions = permissions ? permissions : [];

    return (
      <LayoutWrapper>
        <Box>
          <PageHeader
            title={<IntlMessages id="pettyCashes.add.title" />}
            style={{ margin: 0 }}
          >
            <Button onClick={() => this.props.history.replace(redirectTo)}>
              <IntlMessages id="button.back" />
            </Button>
          </PageHeader>

          <Tabs defaultActiveKey="1">
            <TabPane tab={<IntlMessages id="pettyCashes.tab.detail" />} key="1">
              <PettyCashCreateForm
                submit={this.handleSubmit}
                pettyCash={this.props.pettyCash}
                permissions={permissions}
                user={user}
                // Petty Cash Type
                pettyCashType={this.props.pettyCashType}
                visibleAddPettyCashType={this.state.visibleAddPettyCashType}
                handleModalAddPettyCashType={this.handleModalAddPettyCashType}
                handleSubmitPettyCashType={this.handleSubmitPettyCashType}
              />
            </TabPane>
            <TabPane
              tab={<IntlMessages id="pettyCashes.tab.attachment" />}
              key="2"
              disabled
            ></TabPane>
          </Tabs>
        </Box>
      </LayoutWrapper>
    );
  }
}

const mapStateToProps = state => {
  return {
    auth: state.auth,
    pettyCash: state.pettyCash,
    pettyCashType: state.pettyCashType
  };
};

export default compose(
  connect(mapStateToProps, { ...actions, storePettyCashType })
)(CreatePettyCash);
